.price {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  &__wrapper  {
    h3 {
      margin-bottom: 52px;
      color: $text-black;
      font-weight: 600;
      font-size: 30px;
      line-height: 40px; 
      text-align: center;
      @include screen(tablet) {
        font-size: 32px;
        line-height: 38px;
      }
      @include screen(laptop) {
        font-size: 56px;
        line-height: 68px;
      }
    }
  }
  &__container  {
    text-align: center;
    padding: 36px 42px;
    background-color: $bg-gray;
    border-radius: 24px;
    @include screen(tablet) {
      padding: 40px 48px;
      border-radius: 32px;
    }
    @include screen(laptop) {
      padding: 40px 60px;
    }
    b {
      display: block;
      background: linear-gradient(87deg, #023BE4 -3.48%, #00D3AD 107.5%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      font-weight: 510;
      font-size: 60px;
      line-height: 72px;
      @include screen(tablet) {
        font-size: 60px;
        line-height: 72px;
      }
      @include screen(laptop) {
        font-size: 140px;
        line-height: 168px;
      }
    }
    small {
      color: $text-secondary;
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      @include screen(laptop) {
        font-size: 56px;
        line-height: 68px;
      }
    }
  }
}