.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  &__title {
    text-align: center;
    font-weight: 500;
    font-size: 32px;
    line-height: 42px;
    span {
      display: block;
      background: linear-gradient(90.02deg, #023BE4 10.4%, #00D3AD 93.79%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    @include screen(tablet) {
      font-size: 36px;
      line-height: 46px;
    }
    @include screen(laptop) {
      font-size: 72px;
      line-height: 94px;
    }
  }
}