.footer {
  padding: 64px 0 40px;
  position: relative;
  &__logo {
    margin-bottom: 16px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    order: 1;
    @include screen(tablet) {
      margin-bottom: 0;
      order: unset;
    }
  }
  &__date {
    margin-bottom: 8px;
    order: 2;
    @include screen(tablet) {
      margin-bottom: 0;
      order: unset;
    }
  }
  &__link {
    order: 3;
    @include screen(tablet) {
      order: unset;
    }
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include screen(tablet) {
      flex-direction: row;
      justify-content: space-between;
    }
    span {
      color: $neutral-200;
      font-size: 12px;
      line-height: 14px;
      @include screen(tablet) {
        line-height: 20px;
      }
      @include screen(laptop) {
        font-size: 16px;
        line-height: 19px;
      }
      @include screen(desktop) {
        font-size: 18px;
        line-height: 24px;
      }
    }
  }
}