.header {
  padding-top: 18px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  @include screen(tablet) {
    padding-top: 20px;
  }
  @include screen(laptop) {
    padding-top: 56px;
  }
  @include screen(desktop) {
    padding-top: 40px;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px 0;
    @include screen(tablet) {
      justify-content: unset;
    }
  }
  &__nav {
    display: none;
    align-items: center;
    justify-content: center;
    gap: 36px;
    a {
      color: $neutral-200;
      font-size: 20px;
      line-height: 22px;
      @include screen(laptop) {
        transition: color $transition-default;
        &:hover {
          color: $color-blue;
        }
      }
    }
    @include screen(tablet) {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
    }
  }
}