.about {
  padding: 91px 0;
  background-color: $bg-black;
  position: relative;
  z-index: 2;
  &__bg {
    position: absolute;
    top: -100vh;
    left: 0;
    opacity: 0;
    width: 100%;
    z-index: -1;
    background-color: $bg-black;
    height: 100vh;
  }
  &__bg2 {
    position: absolute;
    bottom: -100vh;
    left: 0;
    opacity: 0;
    width: 100%;
    background-color: $bg-black;
    height: 100vh;
  }
  @include screen(tablet) {
    padding: 98px 0;
  }
  @include screen(laptop) {
    padding: 98px 0 216px 0;
  }
  @include screen(desktop) {
    padding: 98px 0 280px 0;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    &-bottom {
      gap: 35px;
      padding-top: 188px;
      @include screen(tablet) {
        padding-top: 238px;
        gap: 60px;
      }
      @include screen(laptop) {
        gap: 123px;
        padding-top: 200px;
      }
      @include screen(desktop) {
        padding-top: 288px;
      }
    }
    ul {
      display: flex;
      flex-direction: column;
      gap: 100px;
      margin-top: 75px;
      @include screen(tablet) {
        gap: 115px;
        margin-top: 125px;
      }
      @include screen(laptop) {
        gap: 140px;
        margin-top: 191px;
      }
      @include screen(desktop) {
        gap: 182px;
      }
      li {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 40px;
        @include screen(tablet) {
          display: grid;
          align-items: center;
          grid-template-columns: 1fr 1fr;
        }
      }
    }
    p {
      color: $text-gray;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      @include screen(tablet) {
        font-size: 24px;
        line-height: 28px;
      }
      @include screen(laptop) {
        font-size: 40px;
        line-height: 52px;
      }
      @include screen(desktop) {
        line-height: 44px;
      }
      span {
        background: linear-gradient(90.02deg, #023BE4 32.27%, #00D3AD 80.47%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }
  &__title {
    text-align: center;
    color: $text-white;
    font-weight: 600;
    font-size: 30px;
    line-height: 40px;
    @include screen(tablet) {
      font-size: 24px;
      line-height: 28px;
    }
    @include screen(laptop) {
      font-size: 56px;
      line-height: 67px;
    }
  }
  &__text {
    gap: 24px;
    h4 {
      margin-bottom: 24px;
      background: linear-gradient(90deg, #1C55FC -25.31%, #00D3AD 40.14%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      @include screen(tablet) {
        font-size: 24px;
        line-height: 28px;
      }
      @include screen(laptop) {
        font-size: 40px;
        line-height: 44px;
      }
    }
    p {
      color: $text-white;
      font-size: 18px;
      line-height: 26px;
      @include screen(tablet) {
        font-size: 16px;
        line-height: 22px;
      }
      @include screen(laptop) {
        font-size: 24px;
        line-height: 33px;
      }
    }
  }
}