.cards {
  min-height: 10000px;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  .scroll-video {
    position: sticky;
    top: 110px;
    left: 0;
    width: 100%;
    object-fit: contain;
    height: 100%;
    max-height: calc(100vh - 110px);
    z-index: 2;
    background-color: $white;
  }
  &__title {
    position: sticky;
    z-index: 2;
    top: 40px;
    white-space: nowrap;
    font-weight: 500;
    font-size: 32px;
    line-height: 40px;
    background: linear-gradient(90.02deg, #023BE4 10.4%, #00D3AD 93.79%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    @include screen(tablet) {
      font-size: 36px;
      line-height: 46.8px;
    }
    @include screen(laptop) {
      font-size: 64px;
      line-height: 70px;
    }
  }
}