.product {
  &__buttons {
    position: relative;
    margin-top: 103px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    border-radius: 12px;
    display: grid;
    overflow: hidden;
    width: 100%;
    &:before {
      content: '';
      position: absolute;
      height: 32px;
      width: 1px;
      background-color: #CACACA;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__gallery {
    .card-slider {
      max-width: 340px;
      &-gallery {
        .card-slider__image {
          display: block;
          margin: 0 auto;
          min-width: 220px;
          max-width: 220px;
          max-height: 220px;
          @include screen(laptop) {
            min-width: 424px;
            min-height: 424px;
          }
          @include screen(desktop) {
            min-width: 408px;
            max-width: 408px;
            max-height: 408px;
          }
        }
      }
      &-thumbnails {
        margin-top: 48px;
        .card-slider__image {
          max-width: 66px;
          max-height: 66px;
          @include screen(laptop) {
            min-width: 132px;
            min-height: 132px;
          }
          @include screen(desktop) {
            max-width: 142px;
            max-height: 142px;
          }
        }
        .slick {
          &-slide {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 10px;
            min-width: 80px;
            min-height: 80px;
    
            border: 1px solid $black;
            border-radius: 13px;
            cursor: grab;
            &:active {
              cursor: grabbing;
            }
            & > div {
              display: flex;
              justify-content: center;
              align-items: center;
              width: 100%;
              height: 100%;
            }
            @include screen(laptop) {
              margin: 0 12px;
              min-width: 200px;
              min-height: 200px;
            }
            @include screen(desktop) {
              min-width: 216px;
              min-height: 216px;
            }
          }
          &-list {
            margin: 0 -10px;
            padding: 0;
            @include screen(laptop) {
              margin: 0 -12px;
            }
          }
          &-track {
            @include screen(laptop) {
              display: flex;
              justify-content: center;
            }
          }
          &-current {
            border-color: $black;
            @include screen(laptop) {
              background-color: $black;
            }
          }
        }
        @include screen(laptop) {
          margin-top: 172px;
        }
      }
      @include screen(tablet) {
        margin-top: 0;
      }
      @include screen(laptop) {
        margin-top: 72px;
        width: 100%;
        max-width: 648px;
      }
      @include screen(desktop) {
        margin-top: 56px;
        max-width: 704px;
      }
    }
  }
  &__container {
    margin-top: 175px;
    align-items: flex-start;
    display: grid;
    grid-template-columns: 656px 1fr;
    padding-right: 108px;
    gap: 100px;
  }
  &__button {
    background-color: #F0F2F3;
    padding: 16px;
    transition: color $transition-default;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    @include screen(tablet) {
      font-size: 20px;
      line-height: 24px;
    }
    &.active {
      color: #194DE7;
    }
  }
  &__blocks {
    margin-top: 32px;
    @include screen(tablet) {
      margin-top: 36px;
    }
    @include screen(laptop) {
      margin-top: 55px;
    }
  }
  &__block {
    display: none;
    @include screen(tablet) {
      max-width: 70%;
    }
    @include screen(laptop) {
      max-width: 50%;
    }
    &.active {
      display: block;
    }
    h3 {
      font-weight: 500;
      color: #2C2C2C;
      font-size: 24px;
      line-height: 26px;
      @include screen(tablet) {
        line-height: 22px;
        font-size: 20px;
      }
      @include screen(laptop) {
        line-height: 31px;
        font-size: 28px;
      }
    }
    p {
      margin-top: 20px;
      font-size: 16px;
      font-weight: 400;
      color: #2C2C2C;
      line-height: 22px;
      @include screen(desktop) {
        font-size: 18px;
        line-height: 28px;
      }
    }
    &__items {
      margin-top: 20px;
      @include screen(tablet) {
        margin-top: 16px;
      }
      @include screen(laptop) {
        margin-top: 20px;
      }
    }
    &__item {
      padding: 24px 0;
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      border-bottom: 1px solid #C8C8C8;
      @include screen(tablet) {
        grid-template-columns: 250px 1fr;
        padding: 20px 0;
      }
      @include screen(laptop) {
        padding: 24px 0;
        grid-template-columns: 300px 1fr;
      }
      h4 {
        font-weight: 400;
        color: #2C2C2C;
        font-size: 12px;
        line-height: 20px;
        @include screen(laptop) {
          font-size: 16px;
          line-height: 22px;
        }
      }
      span {
        color: #2C2C2C;
        font-weight: 400;
        font-size: 12px;
        line-height: 20px;
        @include screen(laptop) {
          font-size: 16px;
          line-height: 22px;
        }
      }
    }
  }
  &__title {
    font-size: 28px;
    color: #2C2C2C;
    font-weight: 500;
    line-height: 31px;
  }
  &__subtitle {
    color: #2C2C2C;
    font-size: 20px;
    font-weight: 400;
    line-height: 22px;
  }
  &__colors, 
  &__models {
    margin-top: 36px;
  }
  &__price {
    margin-top: 36px;
    font-size: 32px;
    color: #2C2C2C;
    font-weight: 500;
    line-height: 35px;
    text-transform: uppercase;
  }
  &__submit {
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    background: linear-gradient(89.82deg, #023BE4 -18.56%, #00D3AD 119.66%);
    width: 100%;
    text-align: center;
    padding: 16px;
    margin-top: 36px;
    border-radius: 100px;
    margin-bottom: 8px;
    color: $white;
  }
  &__labels {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  &__label {
    cursor: pointer;
    margin-top: 16px;
    position: relative;
    span {
      display: block;
      position: relative;
      width: 44px;
      border-radius: 50%;
      overflow: hidden;
      height: 44px;
      &:before {
        content: '';
        position: absolute;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        box-shadow: 0px 4px 4px 0px #00000033 inset;
        transition: transform $transition-default;
      }
      &:after {
        content: '';
        position: absolute;
        width: 44px;
        border-radius: 50%;
        opacity: 0;
        transition: opacity $transition-default;
        height: 44px;
        border: 2px solid #336DE9;
        transform-origin: center;
      }
    }
    &-black {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #2C2C2C;
        }
      }
    }
    &-white {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #FFFFFF;
        }
      }
    }
    &-blue {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #1471DD;
        }
      }
    }
    &-purple {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #9A41B9;
        }
      }
    }
    &-red {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #E14A4A;
        }
      }
    }
    &-green {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #279D64;
        }
      }
    }
    &-pink {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #ED8E8E;
        }
      }
    }
    &-gray {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #F4F4F4;
        }
      }
    }
  }
  &__input {
    position: absolute;
    width: 0;
    height: 0;
    visibility: hidden;
    &:checked {
      & + span {
        &:after {
          opacity: 1;
        }
        &:before {
          transform: scale(0.75);
        }
      }
    }
  }
  &__tip {
    margin-top: 8px;
    color: #A4A4A4;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
  }
  &__list {
    position: absolute;
    background-color: #F0F2F3;
    padding: 0 16px 14px;
    border-radius: 0 0 12px 12px;
    width: 100%;
    left: 0;
    display: none;
    &.active {
      display: block;
    }
  }
  &__option {
    cursor: pointer;
    margin-top: 16px;
    font-size: 16px;
    font-weight: 600;
    line-height: 19px;
    transition: color $transition-default;
    &:hover {
      color: $color-blue;
    }
    &.active {
      color: $color-blue;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  &__selected {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 14px 16px;
    background-color: #F0F2F3;
    border-radius: 12px;
    cursor: pointer;
    &.active {
      border-radius: 12px 12px 0 0;
      svg {
        transform: rotate(180deg);
      }
    }
    span {
      font-size: 16px;
      font-weight: 600;
      line-height: 19px;
    }
    input {
      position: absolute;
      width: 0;
      height: 0;
      visibility: hidden;
    }
    svg {
      transition: transform $transition-default;
    }
  }
  &__select {
    position: relative;
    margin-top: 16px;
  }
}