.products {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
  min-height: 100vh;
  padding-bottom: 80px;
  &__text {
    margin-bottom: 32px;
    text-align: center;
    font-weight: 600;
    font-size: 30px;
    line-height: 40px;
    @include screen(tablet) {
      margin-bottom: 28px;
      font-size: 32px;
      line-height: 38px;
    }
    @include screen(laptop) {
      margin-bottom: 40px;
      font-size: 56px;
      line-height: 68px;
    }
    @include screen(desktop) {
      margin-bottom: 52px;
    }
  }
  &__list  {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    @include screen(tablet) {
      flex-direction: row;
      gap: 20px;
    }
    @include screen(desktop) {
      gap: 24px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    span {
      padding: 16px 40px;
      background: linear-gradient(89.71deg, #023BE4 -3.09%, #00D3AD 109.08%);
      border-radius: 100px;
      color: $white;
      font-size: 20px;
      font-weight: 500;
      line-height: 24px;
      @include screen(tablet) {
        padding: 10px 40px;
        font-size: 14px;
        line-height: 24px;
      }
      @include screen(laptop) {
        padding: 20px 40px;
        font-size: 24px;
        line-height: 26px;
      }
    }
  }
}