.faq {
  min-height: 100vh;
  &__wrapper  {
    h3 {
      margin-bottom: 28px;
      text-align: center;
      color: $text-black;
      font-weight: 600;
      font-size: 30px;
      line-height: 40px;
      @include screen(tablet) {
        font-size: 32px;
        line-height: 38px;
      }
      @include screen(laptop) {
        margin-bottom: 48px;
        font-size: 56px;
        line-height: 67px;
      }
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  &__item {
    &.active {
      .faq__dropdown {
        display: block;
      }
      &:after {
        transform: rotate(180deg);
      }
    }
    position: relative;
    padding: 20px 64px 20px 24px;
    background-color: $bg-gray;
    border-radius: 12px;
    color: $text-black;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    cursor: pointer;
    @include screen(tablet) {
      font-size: 32px;
      line-height: 38px;
    }
    @include screen(desktop) {
      font-size: 28px;
      line-height: 30px;
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      right: 24px;
      top: 16px;
      background-image: url("/assets/images/arrow-down-24.svg");
      background-repeat: no-repeat;
      width: 24px;
      height: 24px;
      transition: all $transition-default;
      @include screen(laptop) {
        background-image: url("/assets/images/arrow-down.svg");
        width: 40px;
        height: 40px;
      }
    }
  }
  &__dropdown  {
    display: none;
    animation-name: slideDown;
    animation-duration: 0.3s;
    cursor: default;
    p {
      padding-top: 16px;
      color: $text-gray;
      font-weight: 400;
    }
  }
}